import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../context/AuthContext";
import "../assets/styles/LoginCard.css";

const LoginCard = () => {
  const navigate = useNavigate();
  const { login } = useAuth();

  useEffect(() => {
    if (!window.Kakao) {
      const script = document.createElement("script");
      script.src = "https://developers.kakao.com/sdk/js/kakao.js";
      script.async = true;
      document.body.appendChild(script);
      script.onload = initializeKakao;
    } else {
      initializeKakao();
    }
  }, []);

  const initializeKakao = () => {
    if (!window.Kakao.isInitialized()) {
      window.Kakao.init(process.env.REACT_APP_KAKAO_JS_KEY);
      console.log("Kakao SDK ì´ˆê¸°í™” ì™„ë£Œ");
    }
  };

  // ë¡œê·¸ì¸ í›„ ë¦¬ë‹¤ì´ë ‰ì…˜ ì²˜ë¦¬ ë¶€ë¶„ ì¶”ê°€
  const handleKakaoLogin = () => {
    window.Kakao.Auth.login({
      success: (authObj) => {
        console.log("ë¡œê·¸ì¸ ì„±ê³µ", authObj);
        login();
        navigate("/search");
      },
      fail: (err) => {
        console.error("ë¡œê·¸ì¸ ì‹¤íŒ¨", err);
      },
    });
  };

  return (
    <div className="login-card-container">
      <div className="card">
        <h2 className="title">ë¡œê·¸ì¸</h2>
        <button onClick={handleKakaoLogin}>ì¹´ì¹´ì˜¤ ë¡œê·¸ì¸</button>
      </div>
    </div>
  );
};

export default LoginCard;
