import React, { useEffect, useRef, useState } from 'react';

const MapComponent = ({ level = 3 }) => {
  const mapContainerRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const [scriptLoaded, setScriptLoaded] = useState(false);
  const [apiLoaded, setApiLoaded] = useState(false); // API ì´ˆê¸°í™” ì—¬ë¶€ ì¶”ê°€
  const [currentPosition, setCurrentPosition] = useState({ lat: 33.450701, lng: 126.570667 });

  // Kakao Maps ìŠ¤í¬ë¦½íŠ¸ ë™ì  ë¡œë”©
  useEffect(() => {
    if (!document.getElementById('kakao-map-script')) {
      const script = document.createElement('script');
      script.id = 'kakao-map-script';
      script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${window.env.REACT_APP_KAKAO_JS_KEY}&autoload=false`;
      script.async = true;
      script.onload = () => {
        console.log("Kakao Maps API ìŠ¤í¬ë¦½íŠ¸ ë¡œë“œ ì™„ë£Œ");
        setScriptLoaded(true);
      };
      script.onerror = () => {
        console.error("Kakao Maps ìŠ¤í¬ë¦½íŠ¸ ë¡œë“œ ì‹¤íŒ¨!");
      };
      document.head.appendChild(script);
    } else {
      setScriptLoaded(true);
    }
  }, []);

  // API ì™„ì „íˆ ë¡œë“œë  ë•Œê¹Œì§€ ê¸°ë‹¤ë¦¬ëŠ” í•¨ìˆ˜
  const waitForKakaoMaps = () => {
    if (window.kakao && window.kakao.maps && window.kakao.maps.Map) {
      console.log("Kakao Maps API ë¡œë“œ ì™„ë£Œ!");
      setApiLoaded(true);
    } else {
      console.warn("Kakao Maps APIê°€ ì•„ì§ ë¡œë“œë˜ì§€ ì•ŠìŒ. 500ms í›„ ì¬ì‹œë„...");
      setTimeout(waitForKakaoMaps, 500);
    }
  };

  useEffect(() => {
    if (scriptLoaded) {
      console.log("window.kakao.maps.load() ì‹¤í–‰...");
      window.kakao.maps.load(waitForKakaoMaps);
    }
    // eslint-disable-next-line
  }, [scriptLoaded]);

  // í˜„ì¬ ìœ„ì¹˜ ê°€ì ¸ì˜¤ê¸°
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setCurrentPosition({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
        },
        (error) => {
          console.error("ìœ„ì¹˜ ì •ë³´ë¥¼ ê°€ì ¸ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.", error);
        }
      );
    } else {
      console.error("ì´ ë¸Œë¼ìš°ì €ì—ì„œëŠ” Geolocationì„ ì§€ì›í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.");
    }
  }, []);

  // API ë¡œë“œ ì™„ë£Œ í›„ ì§€ë„ ìƒì„±
  useEffect(() => {
    if (apiLoaded && mapContainerRef.current) {
      console.log("ì§€ë„ ìƒì„± ì‹œì‘...");
      if (!mapInstanceRef.current) {
        const container = mapContainerRef.current;
        const options = {
          center: new window.kakao.maps.LatLng(currentPosition.lat, currentPosition.lng),
          level: level,
        };
        mapInstanceRef.current = new window.kakao.maps.Map(container, options);
        console.log("ì§€ë„ ìƒì„± ì™„ë£Œ!");
      } else {
        // ì§€ë„ ì¤‘ì‹¬ ì—…ë°ì´íŠ¸
        const newCenter = new window.kakao.maps.LatLng(currentPosition.lat, currentPosition.lng);
        mapInstanceRef.current.setCenter(newCenter);
        console.log("ì§€ë„ ì¤‘ì‹¬ ì—…ë°ì´íŠ¸");
      }
    }
  }, [apiLoaded, currentPosition, level]);

  return (
    <div
      ref={mapContainerRef}
      style={{ width: "100%", height: "100%" }}
    />
  );
};

export default MapComponent;
